#home-page-second
{
    background-color: white; 
    min-height: 100vh;
    background-size: 100%;
    overflow: visible;
}

#home-page-second-slides
{
    width: 80%;
    height: 65vh;
    margin-left: 10%;
    display: none;
}

#home-page-second-container
{

}

#home-page-second-slide
{
    
}

#home-page-second-slide-caption
{
    text-align: center;
    color: rgb(44, 44, 44);
    font-size: 3rem;
}

#home-page-second-catalog
{
    margin: 3rem 0 5rem 20%;
    width: 60%;
    min-height: 100vh;
    overflow: visible;
}

#home-page-second-slide-image
{
    height: 65vh;
}

#home-page-second-title
{
    text-align: center;
    margin-top: 3rem;
    font-size: 3rem;
}

.accordian-title
{
    padding: 1rem;
    display: inline-block;
}

.accordian
{
    width: 100%;
    background-color: lavender;
    cursor: pointer;
    margin-bottom: 1px;
}

.accordian:hover
{
    background-color: rgb(224, 219, 255);
}

.accordian-button
{
    float: right;
    padding: 1rem;
}

.accordian-content
{
    padding: 1rem;
    border-top: 1px solid white;
}

.accordian-blurb-text
{
    text-align: center;
    margin: 1rem 10% 1rem 10%;
}

.accordian-content img
{
    margin-top: 1rem;
    width: 60%;
    margin-left: 20%;
    border-radius: 5px;
}